    font-weight: normal;
    font-style: normal;

    text-align: center;
    padding-top: 12px;
    
    color: #f6d6bd;

    transition: 0.3s;
    -webkit-transition: 0.3s;

    opacity: 0;
    visibility: hidden;
}

.page-title.left {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
}

.page-title.right {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
}

.page-title.show {
    transition: 0.3s;
    -webkit-transition: 0.3s;

    opacity: 1;
    visibility: visible;

    -webkit-transform: translateX(0px);

            transform: translateX(0px);
}

