}

/* React Modal */
.ReactModal__Content {
    opacity: 0;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.ReactModal__Content--after-open{
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transition: 
        opacity 50ms ease-in-out,
        -webkit-transform 350ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transition: 
        opacity 50ms ease-in-out,
        -webkit-transform 350ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transition: 
        opacity 50ms ease-in-out,
        transform 350ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transition: 
        opacity 50ms ease-in-out,
        transform 350ms cubic-bezier(0.175, 0.885, 0.32, 1.275),
        -webkit-transform 350ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.ReactModal__Content--before-close{
    opacity: 0;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    -webkit-transition: 
        opacity 300ms ease-in-out,
        -webkit-transform 300ms ease-out;
    transition: 
        opacity 300ms ease-in-out,
        -webkit-transform 300ms ease-out;
    transition: 
        opacity 300ms ease-in-out,
        transform 300ms ease-out;
    transition: 
        opacity 300ms ease-in-out,
        transform 300ms ease-out,
        -webkit-transform 300ms ease-out;
}

.ReactModal__Overlay {
    opacity: 0;
}

.ReactModal__Overlay--after-open{
    opacity: 1;
    -webkit-transition: 
        opacity 200ms ease-in-out;
    transition: 
        opacity 200ms ease-in-out;
}

.ReactModal__Overlay--before-close{
    opacity: 0;
    -webkit-transition: 
        opacity 250ms ease-in-out;
    transition: 
        opacity 250ms ease-in-out;
}

.modal {
    background-color: rgba(0, 0, 0, 0);

    box-sizing: border-box;
    image-rendering: pixelated;
    image-rendering: -moz-crisp-edges;
    image-rendering: crisp-edges;
}

.lg-modal-size {
    position: absolute;

    top: 10%;
    bottom: 10%;
    left: 20%;
    right: 20%;
}

.sm-modal-size {
    position: absolute;

    top: 5%;
    bottom: 5%;
    left: 0%;
    right: 0%;
}

.modal:focus {
    outline: none;
}

.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
}

.modal-title-media {
    background-color: #08141e;
    width: 100%;
    height: 100%;
}

.modal-media-loading {
    position: absolute;
    left: 0;
    right: 0;

    font-family: "m5x7";
    font-size: 32px;
    font-weight: normal;
    font-style: normal;

    text-align: center;
    color: white;
    /*background-color: #08141e;*/
    opacity: 1;
}

.modal-media-loading.exit {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
    opacity: 0;
    -webkit-transition: opacity 300ms linear, -webkit-transform 500ms ease-in;
    transition: opacity 300ms linear, -webkit-transform 500ms ease-in;
    transition: transform 500ms ease-in, opacity 300ms linear;
    transition: transform 500ms ease-in, opacity 300ms linear, -webkit-transform 500ms ease-in;
}

/* page-tab transition */
.page-tab-transition-appear {
    opacity: 0;
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
    z-index: 1;
}

.page-tab-transition-appear.page-tab-transition-appear-active {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);

    -webkit-transition: 
        opacity 300ms linear,
        transform 600ms cubic-bezier(0.19, 1, 0.22, 1);
    -webkit-transition: 
        opacity 300ms linear,
        -webkit-transform 600ms cubic-bezier(0.19, 1, 0.22, 1);
    transition: 
        opacity 300ms linear,
        -webkit-transform 600ms cubic-bezier(0.19, 1, 0.22, 1);
    transition: 
        opacity 300ms linear,
        transform 600ms cubic-bezier(0.19, 1, 0.22, 1);
    transition: 
        opacity 300ms linear,
        transform 600ms cubic-bezier(0.19, 1, 0.22, 1),
        -webkit-transform 600ms cubic-bezier(0.19, 1, 0.22, 1);
}

